import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import kz_Home from "../views/kz_Home.vue";
import ru_Home from "../views/ru_Home.vue";
import App from "../App.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/ru",
    alias: "/",
    name: "ru_Home",
    component: ru_Home
  },
  {
    path: "/kz",
    alias: "/",
    name: "kz_Home",
    component: kz_Home
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import("../views/About.vue")
  },
  {
    path: "/situation",
    name: "Situation",
    component: () => import("../views/Situation.vue")
  },
  {
    path: "/dostup_k_informacii",
    name: "Article_1",
    component: () => import("../views/Article_1.vue")
  },
  {
    path: "/diffamatsiya",
    name: "Article_2",
    component: () => import("../views/Article_2.vue")
  },
  {
    path: "/deti_v_smi",
    name: "Article_3",
    component: () => import("../views/Article_3.vue")
  },
  {
    path: "/pravo_na_izobrajenie",
    name: "Article_4",
    component: () => import("../views/Article_4.vue")
  },
  {
    path: "/glasnost",
    name: "Article_5",
    component: () => import("../views/Article_5.vue")
  },
  {
    path: "/avtorskoe_pravo",
    name: "Article_6",
    component: () => import("../views/Article_6.vue")
  },
  {
    path: "/accreditaciya", 
    name: "Article_7",
    component: () => import("../views/Article_7.vue")
  },
  {
    path: "/internet", 
    name: "Article_8",
    component: () => import("../views/Article_8.vue")
  },
  {
    path: "/deyatelnost_jurnalista",
    name: "Article_9",
    component: () => import("../views/Article_9.vue")
  },
  {
    path: "/goszakaz",
    name: "Article_10",
    component: () => import("../views/Article_10.vue")
  },
  {
    path: "/registraciya_smi",
    name: "Article_11",
    component: () => import("../views/Article_11.vue")
  },
  {
    path: "/samoregulaciya",
    name: "Article_12",
    component: () => import("../views/Article_12.vue")
  },
  {
    path: "/status", 
    name: "Article_13",
    component: () => import("../views/Article_13.vue")
  }

];

const router = new VueRouter({
  routes
});

export default router;

Vue.config.productionTip = false;

var shared = new Vue({
  data: {
    global_kz: true, //default language
    global_ru: false
  }
});

shared.install = function() {
  Object.defineProperty(Vue.prototype, "$global", {
    get() {
      return shared;
    }
  });
};

Vue.use(shared);

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')

